@import url('https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap');
body {
    box-sizing: border-box;
}
p {
    margin: 0;
}
.container {
    position: relative;
    height: 100vh;
    max-width: 1024px;
    overflow-x: hidden;
    &::-webkit-scrollbar{
        display: none;
    }
    .input-btn{
        font-size: 1rem;
    }
}
.main-logo{
    display: inline-block;
    .logo{
        cursor: pointer;
    }
}
.nav-bar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .inputGp{
        height: 50px;
    }
    .bin-box{
        width: 130px;
    }
}

.mainList{
    position: relative;
    .menu{
        .active { 
            border-color: #dee2e6 #dee2e6 #fff;
        }
        .cate-list {
            font-size: 1.5rem;
            font-family: 'Nanum Pen Script', cursive;
            color: black;
            &:active {
                border-color: #dee2e6 #dee2e6 #fff;
            }
        }
    }
}
.random-img-box{
    position: relative;
    height:500px;
    .random-img{
        position: absolute;
        width: 100%;
        height: 100%;
        overflow: hidden;
        cursor: pointer;
    }
    .titleName{
        font-size: 3rem;
        color: white;
        font-family: 'Nanum Pen Script', cursive;
        position: absolute;
        z-index: 5;
        bottom: 20px;
        margin: 0 auto;
    }
}
.slick-prev{
    z-index: 5;
    &::before{
        position: relative;
        color: #fff !important;
        left: 60px;
    }
}
.slick-next:before{
    color: #fff !important;
    position: relative;
    right: 60px;
}


.travel-img{
    position: absolute;
    top: 15vh;
    z-index: -1;
    opacity: 0.5;
}
.listGp{
    padding-left: 0.7rem;
}

.list{
    font-size: 1.2rem;
    font-family: 'Nanum Pen Script', cursive;
    .img-box{       
        margin: auto 0;
        .img {
            width: 150px;
            height: 150px;
            &.noImg {
                background: rgba(0,0,0,0.1);
                font-size: 1.5rem;
            }
        } 
    }
         
    .list-text {
        padding: 1rem;
        .title {
            font-size: 1.8rem;
        }
    }
}
.btn {
    font-size: 1.8rem;
    padding: 0.3rem 1rem;
    font-weight: 600;
    &:hover{
        background: rgba(0,0,0,0.3);
    }
}
.list-box {
    font-family: 'Nanum Pen Script', cursive;
    font-size: 2rem;
    text-align: center;
    padding: 1rem;
    .list-btn{
        background: none;
        border: none;
    }
    .listNum{
        display: inline-block;
        text-align: center;
        list-style: none;
        // margin: auto;
        .num{
            display: inline-block;
            margin: 0 1rem;
            cursor: pointer;
            &:hover {
                text-decoration: underline;
            }
        }
        
    }
}
.detail{
    font-family: 'Nanum Pen Script', cursive;
    font-size: 20px;
    .Main{
        display: flex;
        position: relative;
        // justify-content: space-between;
        max-height: 300px;
        overflow-y: hidden;
        .img {
            position: relative;
            width: 50vw;
            max-width: 300px;
            height: 50vw;
            max-height: 300px;
        }
        .overView {
            position: absolute;
            right: -20px;
            bottom: -50px;
            font-size: 1.2rem;
            width: 50vw;
            height: 100%;
            overflow-y: scroll;
            &::-webkit-scrollbar-track{
                -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.1);
                border-radius: 10px;
                background-color: #f5f5f5;
            }
            &::-webkit-scrollbar{
                width: 12px;
                background-color: #F5F5F5;
            }
            &::-webkit-scrollbar-thumb{
                border-radius: 10px;
                -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.1);
                background-color: rgb(190, 190, 190);
            }
        }
    }
    .title{
        font-size: 3rem;
        border-top: 1px solid #ced4da;
        border-bottom: 1px solid #ced4da;
    }
    
    .item{
        .list {
            font-weight: bold;
        }
    }
    
}
.slick-next:before, .slick-prev:before {
    color: #000;
}
.slick-next:before {
    content: "\2192" !important;
}
.slick-dots{
    // display: flex;
    // justify-content: space-between;
    width:100%;
    position: relative;
    // top: 58vh;
    li{
        margin:0 !important;
        width: 100px !important;
        margin-right: 17px !important;
    }
    .typeList.list {
        width: 100px;  
        border-radius: 15px;      
        border: 1px solid black; 
    }

}
.random{
    .title{
        font-family: 'Nanum Pen Script', cursive;
        font-size: 3rem;
    }    
}
